.container {
    padding: 0px 15px;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
        @media screen and (min-width: 480px) {
            width: 480px;
        }
        @media screen and (min-width: 768px) {
            width: 768px;
        }
        @media screen and (min-width: 1280px) {
            width: 1280px;
        }
}

